.header-nav{
    //background-color: #777;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 6rem;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transition: top 0.6s;
    z-index: 100000000;
 
  
 
    .burger_hamburger__21cuQ {
        padding: 0 !important;
        margin: 0 !important;
        align-self: center;
        display: none !important;
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            display: block  !important;
         }
     
         @media only screen and (max-width: 56.25em) { // tabport 900px
            display: block  !important;
         }

    }

    &--white{
        background-color: white;
        box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 2.5px -2px;

    }
    &__logo{
        padding:.5rem;
    };

    &__image{
        width: 4rem;
        animation: animatedLogo .5s ease-out;

    }
    &__menu{
        height:100%;
        display: flex;
        justify-content: center;
        align-items: baseline;
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            display: none;
         }
     
         @media only screen and (max-width: 56.25em) { // tabport 900px
            display: none;
         }
    }
    &__list{
        display: flex;
        list-style: none;
        font-size: 1.8rem;
        
        &--whiteStyle li a{
            color:#373F49 !important;
            
        }
        &--whiteStyle {
            .header-nav__item:hover::before {
                border-bottom:2px solid #55c57a !important;
            }
        }
       
    }
    
    &__item{
        color:white;
        margin:2rem;
        position: relative;
        display: flex;
        justify-content: center;
        transition: all .2s ease-in-out;
        animation: animatedMenu .3s ease-out;
        animation-fill-mode: backwards;

        &--1{
            animation-delay: .5s;
        }
        &--2{
            animation-delay: .6s;
        }
        &--3{
            animation-delay: .7s;
        }
      
        &:before {
            content : "";
            position: absolute;
            bottom  : -3px;
            height  : 1px;
            width   : 50%;  /* or 100px */
          }
          &:hover::before{
            border-bottom:2px solid white;
        }


       
        
    }
    &__btn{
        height:2rem;
        border-radius: .5rem;
        text-transform: uppercase;
        font-size:1.3rem;
        outline: none;
        font-weight: 400;
        transition: all .2s;
        display: #373f49;
        background-color: white;
        color: #55c57a;
        border: 1px solid #28b485;
        padding: 1.3rem 1.8rem;
        animation: animatedMenu .3s ease-out;
        animation-fill-mode: backwards;
        animation-delay: .8s;

        &:hover{
          background-color: #55c57a;
          color: white;
          border: 1px solid #55c57a;

        }
        &:active,
        &:focus {
            outline: none;
        } 

        &--whiteStyle{
            background-color: #55c57a;
            color: white;
            box-shadow: 0 .1rem .2rem rgba(black,.2);
            border: 1px solid #55c57a;
            &:hover{
                background-color: white;
                color: #55c57a;
                border: 1px solid #28b485;

              }
        }
    }

    &__link{
        color: #373F49;
        text-decoration: none;
    }

    &--hidden{
        top:-100px;

       
    }
}


@keyframes animatedMenu {
    from{
        opacity: 0;
        transform: translateY(20px);
    }
    to{
        opacity: 100%;
        transform: translateX(0);
    }
}



@keyframes animatedLogo {
    from{
        opacity: 0;
        transform: translateY(-20px);

    }
    to{
        opacity: 100%;
        transform: translateY(0);

    }
}



.menuBar{
    background-color: #f7f7f7;
    width: 50vw;
    height: 198vh;
    position: absolute;
    z-index: -1;
    transition: all .5s;
    right: -317px;
    box-shadow: rgba(0, 0, 0, 0.15) 14px 17px 35.5px 15px;
    display: none !important;

 
     @media only screen and (max-width: 56.25em) { // tabport 900px
        display: block !important;
        width: 30vw;
     }
     @media only screen and (max-width: 37.5em) {  //phone/600px 
        display: block !important;
        width: 50vw !important;

     }

    &__active{
        transform: translateX(-317px);
    }

    &__inactive{
        
        transform: translateX(195px);
        display: block;
    }


    &__menu{
    color: black;
    display: flex;
    position: absolute;
    height: 277px;
    width: 100%;
    top: 52%;
    letter-spacing: 0;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    }
    &__list{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        align-self: center;
        align-content: end;
        height: 100%;
        font-size: 1.8rem;
        list-style: none;

    
    }
    &__link{
        color: #373F49;
        text-decoration: none;
    }
    &__btn{
        height:2rem;
        border-radius: .5rem;
        text-transform: uppercase;
        font-size:1.3rem;
        outline: none;
        font-weight: 400;
        transition: all .2s;
        display: #373f49;
        background-color: #55c57a;
        color: white;
        box-shadow: 0 .1rem .2rem rgba(black,.2);
        border: 1px solid #55c57a;
        padding: 1.3rem 1.8rem;
    }
}