.heading{
    padding: 6rem;
    display: flex;
    flex-grow: 1;
    justify-content: space-around;
    align-items: flex-end;


  
    @media only screen and (max-width: 37.5em) {  //phone/600px 
        align-items: start;
        padding: .5rem;
    }

    @media only screen and (max-width: 56.25em) { // tabport 900px
        align-items: center;

    }

    @media only screen and (max-width: 75em) { // tapland 1200px
        align-items: center;
        justify-content: center;
    }
    
    @media only screen and (min-width: 112.5em) { //desktop 1800px
    }


    &-box{
        height:50rem;
        &__grating{
            font-size: 1.6rem;
            padding-bottom: 1rem;
            animation: fallAnimation .5s ease-out 1s;
            animation-fill-mode: backwards;
            
            @media only screen and (max-width: 37.5em) {  //phone/600px 
                font-size: 2rem ;
                
            }

            @media only screen and (max-width: 25em) { // smallphone 900px
                font-size: 2.2rem ;
            }
        

        }
        &__primary{
            font-size: 8rem ;
            margin-bottom: 4rem;
            
            @media only screen and (max-width: 75em) { // tapland 1200px
                align-items: center;
                font-size: 7rem ;

            }
          
        
            @media only screen and (max-width: 56.25em) { // tabport 900px
                font-size: 5rem ;
                align-items: center;
                display: flex;
                flex-direction: column;
        
            }

  
            @media only screen and (max-width: 37.5em) {  //phone/600px 
                font-size: 4rem ;
                display: flex;
                flex-direction: column;
            }

            @media only screen and (max-width: 25em) { // smallphone 900px
                font-size: 3.5rem ;
            }
        
           
            &--main{
                display: block;
                padding-bottom: .5rem;
                animation: fallAnimation .5s ease-out;
                animation-fill-mode: backwards;
                animation-delay: 1.2s;
            }
            &--sub{
                display: block;
                animation: fallAnimation .5s ease-out;
                animation-fill-mode: backwards;
                animation-delay: 1.4s;
            }
        }
    
        &__description{
    
            display: block;
            font-size: 1.8rem;
            width: 50rem;
            line-height:1.65;
            animation: fallAnimation .5s ease-out;
            animation-fill-mode: backwards;
            animation-delay: 1.6s;
           
            @media only screen and (max-width: 56.25em) { // tabport 900px
                width: 80%;

            }

  
            @media only screen and (max-width: 37.5em) {  //phone/600px 
                width: 75%;
                font-size: 2rem;

            }
            @media only screen and (max-width: 25em) { // smallphone 900px
                width: 90%;
                font-size: 2.5rem;

            }
            
        }
    
        &__btn{
            padding: 4rem 0;
            display: flex;
            animation: fallAnimation .5s ease-out;
            animation-fill-mode: backwards;
            animation-delay: 1.8s;
            .btn:first-child{
                margin-right: 2rem;
            }
            height:90vh;


            
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            width: 90%;
            flex-direction: column;    
            align-items: center;
        }
 
        @media only screen and (max-width: 56.25em) { // tabport 900px
            width: 90%;
            flex-direction: column;
            align-items: center;
    
        }

        }
    

        @media only screen and (max-width: 37.5em) {  //phone/600px 
            height: 27rem;
            align-items: center;
            justify-content: center;
            display: flex;
            flex-direction: column;    
        }
 
        @media only screen and (max-width: 56.25em) { // tabport 900px
            height: 27rem;
            align-items: center;
            justify-content: center;
            display: flex;
            flex-direction: column;     align-items: center;
    
        }
    
    }
    


    &-featuredImg{

        align-self: flex-end;
        &__img{
            width: 30rem;
            animation: fallAnimation .5s ease-out;
            animation-fill-mode: backwards;
            animation-delay: 1.5s;
        }
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            display: none;
     
         }

         @media only screen and (max-width: 56.25em) { // tabport 900px
            display: none;
            
    
        }



    }
    

}


@keyframes fallAnimation {
    from{
        opacity: 0;
        transform: translateY(-20px);
    }
    to{
        opacity: 100%;
        transform: translateY(0);
    }
}