
*,
*::after,
*::before{
    padding: 0;
    margin:0;
    box-sizing: inherit;
}

html{
    /* 62.5% = 10px / 16px; 1rem = 10px */
    font-size: 62.5%;
  
  
  


    
    @media only screen and (min-width: 112.5em) { //desktop 1800px
        font-size: 75%  !important; //1rem = 12, 12/16
    }
    
    @media only screen and (max-width: 75em) { // tapland 1200px
        font-size: 50%  !important; //1 rem = 8px, 8/16 = 50%
    }
    @media only screen and (max-width: 56.25em) { // tabport 900px
        font-size: 56.25%  !important; //1 rem = 9px, 9/16 = 50%
    } 
    
    @media only screen and (max-width: 37.5em) {  //phone/600px 
        font-size: 45%  !important; 
    }

    @media only screen and (max-width: 25em) { // smallphone 900px
        font-size: 35.25% !important; //1 rem = 9px, 9/16 = 50%
    }
}

body{
    box-sizing: inherit;
    font-family: Lato, sans-serif;
    font-weight: 400;
    font-size: 1.6rem;
    color:#373f49;
}












