.btn{
    height:2rem;
    padding: 2rem 4rem;
    border-radius: 1rem;
    text-transform: uppercase;
    font-size:1.6rem;
    outline: none;
    border :none;
    font-weight: 500;
    transition: all .2s;
    display: block;
          
    @media only screen and (max-width: 37.5em) {  //phone/600px 
        width: 90%;
        margin: 0 0 20px 0 !important;
        padding: 2rem 0;

    }

    @media only screen and (max-width: 56.25em) { // tabport 900px
        width: 90%;
        padding: 2rem 0;
        margin: 0 0 20px 0 !important;
    }
           
    @media only screen and (max-width: 37.5em) {  //phone/600px 
        width: 90%;
        margin: 0 0 20px 0 !important;
        padding: 2rem 0;

    }

    @media only screen and (max-width: 25em) { // smallphone 900px
        width: 100%;
        font-size: 2.2rem;
    }

    &:hover{
        transform: translateY(-3px);
        box-shadow: 0 1rem 2rem rgba(black,.2);
    }
    &:active,
    &:focus {
        outline: none;
        transform: translateY(-1px);
        box-shadow: 0 .5rem 1rem rgba(black,.2);
    }
    &-white{
        color:#777;
        background-color:white;
        border: 2px solid #55c57a;
        &:hover{
            background-color: #55c57a;
            border:none;
            color:white;
         
        }
     
    }

    &-green{
        color:white;
        background-color:#55c57a;
        &:hover{
            background-color: #28b485;
        }
    }
}

