.about{
    padding: 8rem;
    display: flex;
    justify-content: center;
    box-shadow: rgba(19, 15, 235, 0.1) 2px 4px 15px 0px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.06);
    &__image-box{
        width: 25vh;
        height:25vh;
   
 
     @media only screen and (max-width: 56.25em) { // tabport 900px
        margin-bottom: 7rem;
        width: 20vh;
        height:20vh;
     }
     
     @media only screen and (max-width: 37.5em) {  //phone/600px 
        margin-bottom: 7rem;
        width: 20vh;
        height:20vh;
     }

     @media only screen and (max-width: 25em) { // smallphone 900px
        width: 15vh;
        height:15vh;
    }
        //margin-right: 4rem;
    }
    &__img{
        width: 100%;
        height:100%;
        border-radius: 50% 0 50% 0;
        border: 4px solid #55c57a;

        
    }
    &__description{
        width: 60%;
        margin-left: 6rem;
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            width: 80%;
            margin-left: 0;

         }
     
         @media only screen and (max-width: 56.25em) { // tabport 900px
            margin-left: 0;
            width: 70%;

         }
    }
    &__heading{
        font-size: 4rem;
        margin-bottom: 1rem;
        @media only screen and (max-width: 56.25em) {  //
            font-size: 3.5rem;
        } 
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            font-size: 3rem;
        }
    }
    &__text{
        padding: .8rem 0;
        text-align: justify;
        font-weight: 400;
        font-size: 1.6rem;
        line-height: 1.65;
        @media only screen and (max-width: 25em) { // smallphone 900px
            font-size: 2.5rem ;
        }
    }

    @media only screen and (max-width: 37.5em) {  //phone/600px 
       flex-direction: column;
       align-items: center;
       padding: 2rem 0;
    }

    @media only screen and (max-width: 56.25em) { // tabport 900px
        flex-direction: column;
        align-items: center;
        padding: 4rem 0;

    }

}


