.socialIcons{
    margin-top: .5rem;
    &__icon{
        font-size: 2.2rem;
        transition: all .2s;
        text-decoration: none;
        &:link,
        &:visited{
            color:#373F49;
        }
        &:not(:last-child){
            margin-right: 1.4rem;
        }
        &:hover,
        &active{
            color:#55c57a;
        }
    }
}