.header{
    height: 90vh;
    display:flex;
    flex-direction: column;
    width: 100%;
    background-repeat: no-repeat;
    background-position: 0 0;
    animation: animatedBackground .8s ease-in-out;
    @media only screen and (max-width: 37.5em) {  //phone/600px 
      height: 100vh;

   }
   

    
}
@keyframes animatedBackground {
    from {
        background-position: 200% -67rem;
      }
      to {
        background-position: 135% -40rem;
      }
}