.contact-section{
    height:90vh;
    width:100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    padding: 20px 0;
    border-top: 1px solid rgba(0, 0, 0, 0.06);
    @media only screen and (max-width: 56.25em) {  //
        height:80vh;
    } 
    @media only screen and (max-width: 37.5em) {  //phone/600px 
        height:70vh;
    }
    &__contactBox{
        height: 60%;
        width: 50%;
        align-self: center;
       display: flex;
       align-items: center;
       flex-direction: column;
     
       margin-top: 12rem;
    }

    &__title{
        font-size: 6rem;
        margin-bottom: 5rem;
       
        @media only screen and (max-width: 56.25em) {  //
            font-size: 5rem;
        } 
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            font-size: 4rem;
        }
    }

    &__description{
        padding: .8rem 0;
        margin-bottom: 5rem;
    
        text-align: justify;
        font-weight: 400;
        font-size: 1.6rem;
        line-height: 1.65;
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            font-size: 2rem ;
        }

        @media only screen and (max-width: 25em) { // smallphone 900px
            font-size: 2rem ;
        }
    }

    &__mailBtn{

    }

    &__footer{
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: space-around;
        height: 8rem;
    }

    &__buildByText{
        &--heart{
            color:red;
        }

    }

    &__socialIcons{

    }



}