.projects{
    padding: 10rem 0;
    display: flex;
    flex-direction: column;
    background-position: 14px 21px;
    background-attachment: fixed;
    overflow: hidden;
    & .project__container {
        &--left{
            align-self: flex-start;
        }

        &--right{
            align-self: flex-end;

        }
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            padding: 0;
      
         }

    }
    &__heading{
        font-size: 4rem;
        text-align: center;
        margin-bottom: 8rem;
        @media only screen and (max-width: 56.25em) {  //
            font-size: 3.5rem;
        } 
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            font-size: 3rem;
        }
    }
}