.project{

    &__container{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60%;
        padding: 2rem 16rem;

        &--left .project__featuredImage {
            order:-1;
          
         
        }
        &--right .project__featuredImage{
            order:1;
          
            @media only screen and (max-width: 56.25em) { // tabport 900px
                order:-1;

            }
            @media only screen and (max-width: 37.5em) {  //phone/600px 
                order:-1;
            }
        }
       

        @media only screen and (max-width: 56.25em) { // tabport 900px
            align-self: center !important;
            padding: 5rem 0;

        }
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            align-self: center !important;
            padding: 3rem 0;
        }

        &--left .project__buildByIcons {
            justify-content: flex-end;
            @media only screen and (max-width: 56.25em) { // tabport 900px
                justify-content: center;
            }
            @media only screen and (max-width: 37.5em) {  //phone/600px 
                justify-content: center;
            }
           
            
    
        }
        &--right .project__buildByIcons{
            justify-content: flex-start;
            @media only screen and (max-width: 56.25em) { // tabport 900px
                justify-content: center;
            }
            @media only screen and (max-width: 37.5em) {  //phone/600px 
                justify-content: center;
            }
         
        }
        @media only screen and (max-width: 56.25em) { // tabport 900px
            width: 85%;
            flex-direction: column;
            align-self: center
        }
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            width: 95%;
            flex-direction: column;
            align-self: center
        }
    
      
       
        
    }


    &__featuredImage{
        height: 30rem;
        width: 850px;
        background-size: cover;
        background-repeat: no-repeat;
        box-shadow: rgba(14, 14, 14, 0.5) 9px 8px 19px -2px;
        z-index: -1;
        &:hover{
            z-index: 1000;
        }
        &:hover ~.project__details .project__description--left{
            transform:translateX(9rem);
           
        @media only screen and (max-width: 56.25em) {  //
            transform:translateX(0);
        }    
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            transform:translateX(0);

        }
           
      
        }
        &:hover ~.project__details .project__description--right{
            transform:translateX(-9rem);
          
            @media only screen and (max-width: 56.25em) { 
                transform:translateX(0);
    
            }
            @media only screen and (max-width: 37.5em) {  //phone/600px 
                transform:translateX(0);
    
            }
        }


        @media only screen and (max-width: 56.25em) {  //
            align-self: center;
            width: 65% !important;

        }
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            align-self: center;
            width: 80% !important;
        }
    }
   
  




    &__details{
        height:30rem;
        width: 60%;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: space-evenly;

        @media only screen and (max-width: 56.25em) {  //p
            padding: 7rem 0;
            width: 65% !important;

        }
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            padding: 5rem 0;
            width: 80% !important;
        }
        
        
    }

    &__name{
        display: flex;
        align-content: center;
        margin-left: 1rem;
        &--left{
            align-self: flex-end;
           
        }

        &--right{
            align-self: flex-start;
        }

      
        @media only screen and (max-width: 56.25em) {  //
            align-self: center;
        }
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            align-self: center;
        }
    }

    &__title{
        text-transform: uppercase;
        font-size: 2rem;
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            font-size: 2.5rem ;
            margin-bottom: 1rem;

        }

        @media only screen and (max-width: 25em) { // smallphone 900px
            font-size: 2.5rem ;
            margin-bottom: 1rem;

        }
    }

    &__links{
        display: flex;
        align-items: center;
      
    }

    &__url{
        margin-left: 1rem;
        text-decoration: none;
        &:link,
        &:visited {
            color: #373F49;
        }

        &:hover,
        &:active{
            color:#55c57a;
        }
    }

    &__description{
        padding: 2rem;
        text-align: justify;
        font-weight: 400;
        font-size: 1.6rem;
        line-height: 1.65;
        background-color: #f7f7f7;
        color:#333;
        border-radius: 5px;
        z-index: 100;
        box-shadow: rgba(14, 14, 14, 0.5) 9px 8px 19px -2px;
        transition: all .5s ease-in-out;

        &--left{
            margin-left: -10rem;
        }

        &--right{
            margin-right: -10rem;
        }
       
        @media only screen and (max-width: 56.25em) { 
            margin: 0;
        }
        @media only screen and (max-width: 37.5em) {  //phone/600px 
            margin: 0;
            font-size: 2rem ;

        }

        @media only screen and (max-width: 25em) { // smallphone 900px
            font-size: 2rem ;
        }

    }
    &__buildByIcons{
       width: 100%;
       display: flex;
    }
    &__buildByIconsImg{
        width:30px;
        height:30px;
        margin: 0 .5rem;
    }
}

/* Hide Play button + controls on iOS */
video::-webkit-media-controls {
    display:none !important;
}